import React, { useEffect, useState } from 'react';
import { Button, Card, Col, Image, ListGroup, Row } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { Link, Navigate, useLocation, useNavigate } from 'react-router-dom';
import { clearCart } from '../actions/cartActions';
import { createOrder } from '../actions/orderActions';
import CheckoutSteps from '../components/CheckoutSteps';
import Message from '../components/Message';
import { ORDER_CREATE_RESET } from '../constants/orderConstants';

const PlaceOrderScreen = () => {
	const dispatch = useDispatch();
	const navigate = useNavigate();

	const cart = useSelector((state) => state.cart);

	const addDecimals = (number) => {
		return (Math.round(number * 100) / 100).toFixed(2);
	};

	const totalItemsPrice = addDecimals(
		+cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0)
	);
	const shippingPrice = addDecimals(+totalItemsPrice > 100 ? 0 : 20);
	const taxPrice = addDecimals(Number(totalItemsPrice * 0.15));
	const totalPrice = addDecimals(
		+totalItemsPrice + +shippingPrice + +taxPrice
	);

	// const addDecimals = (num) => {
	// 	return (Math.round(num * 100) / 100).toFixed(2);
	// };

	// cart.itemsPrice = addDecimals(
	// 	cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0)
	// );

	// cart.shippingPrice = addDecimals(cart.itemsPrice > 100 ? 0 : 100);
	// cart.taxPrice = addDecimals(Number(0.15 * cart.itemsPrice));

	// cart.totalPrice = addDecimals(
	// 	Number(cart.itemsPrice) +
	// 		Number(cart.shippingPrice) +
	// 		Number(cart.taxPrice)
	// );

	const orderCreate = useSelector((state) => state.orderCreate);
	const { order, success, error } = orderCreate;

	useEffect(() => {
		if (success) {
			dispatch({ type: ORDER_CREATE_RESET });
			navigate(`/orders/${order._id}`);
		}
	}, [navigate, success, order]);

	const placeOrderHandler = () => {
		dispatch(
			createOrder({
				orderItems: cart.cartItems,
				shippingAddress: cart.shippingAddress,
				paymentMethod: cart.paymentMethod,
				itemsPrice: totalItemsPrice,
				shippingPrice: shippingPrice,
				taxPrice: taxPrice,
				totalPrice: totalPrice
			})
		);
	};

	return (
		<>
			<CheckoutSteps step1 step2 step3 step4 />
			<Row>
				<Col md={8}>
					<ListGroup variant="flush">
						<ListGroup.Item>
							<h2>Shipping</h2>
							<p>
								<strong>Address: </strong>
								{cart.shippingAddress.address},{' '}
								{cart.shippingAddress.city},{' '}
								{cart.shippingAddress.postalCode},{' '}
								{cart.shippingAddress.country}
							</p>
						</ListGroup.Item>

						<ListGroup.Item>
							<h2>Payment Method</h2>
							<p>
								<strong>Method: </strong>
								{cart.paymentMethod}
							</p>
						</ListGroup.Item>

						<ListGroup.Item>
							<h2>Order Items</h2>
							{cart.cartItems.length === 0 ? (
								<Message>Your cart is empty</Message>
							) : (
								<ListGroup variant="flush">
									{cart.cartItems.map((item, index) => (
										<ListGroup.Item key={index}>
											<Row>
												<Col md={1}>
													<Image
														src={item.image}
														alt={item.name}
														fluid
														rounded
													/>
												</Col>
												<Col>
													<Link to={`/product/${item.product}`}>
														{item.name}
													</Link>
												</Col>
												<Col md={4}>
													{item.qty} x ${item.price} = $
													{(item.qty * item.price).toFixed(2)}
												</Col>
											</Row>
										</ListGroup.Item>
									))}
								</ListGroup>
							)}
						</ListGroup.Item>
					</ListGroup>
				</Col>
				<Col md={4}>
					<Card>
						<ListGroup variant="flush">
							<ListGroup.Item>
								<h2>Order Summary</h2>
							</ListGroup.Item>
							<ListGroup.Item>
								<Row>
									<Col>Items</Col>
									<Col>${totalItemsPrice}</Col>
								</Row>
							</ListGroup.Item>
							<ListGroup.Item>
								<Row>
									<Col>Shipping</Col>
									<Col>${shippingPrice}</Col>
								</Row>
							</ListGroup.Item>
							<ListGroup.Item>
								<Row>
									<Col>Tax</Col>
									<Col>${taxPrice}</Col>
								</Row>
							</ListGroup.Item>
							<ListGroup.Item>
								<Row>
									<Col>Total</Col>
									<Col>${totalPrice}</Col>
								</Row>
							</ListGroup.Item>

							{(error || success) && (
								<ListGroup.Item>
									{error && (
										<Message variant={'danger'}>{error}</Message>
									)}
									{success && (
										<Message variant={'success'}>{'success'}</Message>
									)}
								</ListGroup.Item>
							)}

							<ListGroup.Item>
								<Button
									type="button"
									className="btn-block"
									disabled={cart.cartItems.length === 0}
									onClick={placeOrderHandler}
								>
									Place Order
								</Button>
							</ListGroup.Item>
						</ListGroup>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default PlaceOrderScreen;
